
.fade-enter-active{transition:all .5s ease
}
.fade-leave-active{transition:all 0s ease
}
.fade-enter,.fade-leave-active{transform:scale(0);opacity:0
}
.el-select-dropdown{z-index:10000 !important
}
#copyedit{min-width:1200px
}
#copyedit .showbox{width:800px;height:700px;overflow:auto;background-color:#fff;position:absolute;left:50%;top:50px;margin-left:-400px;z-index:10000
}
#copyedit .mask{position:absolute;left:0;right:0;bottom:0;top:0;background-color:#000;opacity:.3;z-index:9999
}
#copyedit .distribution{width:800px;height:400px;position:absolute;z-index:10000;background-color:#fff;left:55%;top:50%;margin-left:-400px;margin-top:-200px;padding-top:80px;padding-left:100px;padding-right:100px
}
#copyedit .distribution .row{margin-bottom:30px
}
#copyedit .distribution .row .distributionit{width:190px;height:50px;margin:0 auto;display:block;margin-top:30px;background-color:#4a8bec;color:#fff;font-size:16px
}
#copyedit .distribution .row .el-input__inner{width:420px;height:60px;font-size:16px
}
#copyedit .distribution .el-icon-close{position:absolute;cursor:pointer;right:20px;top:20px
}
#copyedit .header{background-color:#fff;overflow:hidden;height:60px;border-bottom:1px solid #cccccc;width:100%;padding:0 26px 0 26px
}
#copyedit .header .back{margin-top:23px;cursor:pointer;display:inline-block;width:20px;height:20px
}
#copyedit .header p{display:inline-block;margin-left:20px
}
#copyedit .header p span{margin-left:10px
}
#copyedit .header p span.change{cursor:pointer;color:#5c94ed;margin-left:20px
}
#copyedit .content{width:100%;background-color:#fff;padding-left:26px
}
#copyedit .content .content_l{float:left;width:75%;border-right:1px solid #cccccc;box-sizing:border-box;padding-right:26px
}
#copyedit .content .content_l .titleinput{width:100%;height:70px;font-size:18px;line-height:70px;margin-top:20px;border:none;outline:none;border-bottom:1px solid #ccc
}
#copyedit .content .content_l .titleinput.subtitleinput{font-size:16px
}
#copyedit .content .content_l .editbox{width:100%;margin-top:40px;margin-bottom:30px
}
#copyedit .content .content_l .tips{margin-bottom:70px;font-size:14px;color:red
}
#copyedit .content .content_r{width:25%;float:right;padding:20px;height:100%
}
#copyedit .content .content_r .gtngroup{width:100%;height:50px;margin-top:420px
}
#copyedit .content .content_r .gtngroup button{width:45%;height:50px;cursor:pointer;font-size:16px;border:none;outline:none;background-color:#4a8bec;color:#fff
}
#copyedit .content .content_r .gtngroup button.save{margin-right:4%
}
#copyedit .content .content_r .gtngroup button.see{margin-left:3%
}
#copyedit .content .content_r .uploadbox{width:100%
}
#copyedit .content .content_r .uploadbox .avatar-uploader{width:100%;height:100%
}
#copyedit .content .content_r .uploadbox .avatar-uploader .el-upload{width:100%;height:100%
}
#copyedit .content .content_r .uploadbox .avatar-uploader .el-upload .avatar{position:relative;width:100%;height:100%;top:50%
}
#copyedit .content .content_r .uploadbox .avatar-uploader-icon{font-size:28px;color:#8c939d;width:100%;height:100%;line-height:200px;text-align:center
}
#copyedit .content .clear{clear:both
}


