
[v-bloak]{display:none
}
.index{height:100%
}
.index .header{padding-left:20px;display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;-ms-flex-pack:justify;justify-content:space-between;height:60px;background-color:#fff
}
.index .header .left img{cursor:pointer
}
.index .header .left p{display:inline-block;margin-left:30px;font-size:14px
}
.index .header .left p span{cursor:pointer
}
.index .header .left .listActive{color:#37394c
}
.index .header .left .listNotActive{color:#dfdfe2
}
.index .header .left .left_return{margin-left:20px;text-decoration:none;color:#000
}
.index .header .left .left_return i{margin-right:10px
}
.index .header .right{height:100%;display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;-ms-flex-pack:justify;justify-content:space-between;position:relative
}
.index .header .right .el-input{width:62%
}
.index .header .right .curIcon{display:inline-block;height:100%;line-height:60px;padding-right:20px
}
.index .header .right .curIcon i{cursor:pointer;color:#a1a7af
}
.index .header .right .addIcon{display:inline-block;height:100%;line-height:60px;padding-right:20px;border-right:1px solid #f5f5f5
}
.index .header .right .addIcon i{cursor:pointer;color:#a1a7af
}
.index .header .right .newHotelList{position:absolute;left:-124px;top:62px;background-color:#fff;box-shadow:1px 1px 5px #c6cbcf
}
.index .header .right .newHotelList .el-input{width:100%
}
.index .header .right .newHotelList li{padding:20px 21px
}
.index .header .right .newHotelList li button{width:100%;height:100%;padding:20px 21px;background-color:#1875f0;color:#fff;border-radius:0
}
.index .header .right .newHotelList li .is-disabled{background-color:#c6cbcf
}
.index .header .right .newHotelList li:nth-child(1){border-bottom:1px solid #f5f5f5
}
.index .header .right .newHotelList li:nth-child(3){padding:0
}
.index .header .right .newList{position:absolute;left:-124px;top:62px;background-color:#fff;box-shadow:1px 1px 5px #c6cbcf
}
.index .header .right .newList li{padding:20px 60px;color:#bfcad2;cursor:pointer
}
.index .header .right .newList li:hover{color:#fff;background-color:#a2c4da
}
.index .header .right .curText{font-size:14px;padding-right:50px
}
.index .header .right .newfade-enter-active,.index .header .right .newfade-leave-active{transition:all .5s
}
.index .header .right .newfade-enter,.index .header .right .newfade-leave-active{opacity:0
}
.index .header .right .el-input{margin-left:20px
}
.index .header .right .el-input i{cursor:pointer
}
.index .header .right .el-input input{outline:none;border:none
}
.index .body{background-color:#eff3f6;min-height:100%
}


