
.hotelFacCorrect{width:100%;padding-left:20px;padding-bottom:30px
}
.hotelFacCorrect .name{margin-bottom:20px;width:70%
}
.hotelFacCorrect .input_text{position:relative;padding-right:24px
}
.hotelFacCorrect .input_text .currency{position:absolute;right:28px;top:7px;font-size:15px
}
.hotelFacCorrect .pic_list{width:100%
}
.hotelFacCorrect .pic_list .uploadbox{margin-top:20px;margin-bottom:20px
}
.hotelFacCorrect .pic_list .uploadbox .el-upload-list li{width:153px;margin-right:15px;height:100px
}
.hotelFacCorrect .pic_list .uploadbox .el-upload-list li .el-upload-list__item-status-label{-webkit-transform:rotate(45deg)
}
.hotelFacCorrect .pic_list .uploadbox .el-upload-list li .el-upload-list__item-status-label .el-icon-check{-webkit-transform:rotate(-45deg) scale(.8)
}
.hotelFacCorrect .pic_list .uploadbox div{display:inline
}
.hotelFacCorrect .pic_list .uploadbox div .el-upload{display:inline-block
}
.hotelFacCorrect .pic_list .uploadbox div .el-upload-list .is-success{width:153px;height:100px
}
.hotelFacCorrect .pic_list .uploadbox div .is-uploading{width:153px;height:100px
}
.hotelFacCorrect .pic_list .uploadbox div .el-upload{width:153px;height:100px;background-color:#b3c5e1;border:none
}
.hotelFacCorrect .pic_list .uploadbox div .el-upload .el-icon-plus{color:#fff;position:relative;top:-24px
}
.hotelFacCorrect .pic_list .uploadbox .el-dialog__wrapper .el-dialog .el-dialog__header{display:inherit !important
}
.hotelFacCorrect .pic_list .uploadbox .el-dialog__body{display:block !important
}
.hotelFacCorrect .row{margin-bottom:35px
}
.hotelFacCorrect .row .row_name{color:#adafc0;font-size:17px
}
.hotelFacCorrect .row .row_p{margin-bottom:11px
}
.hotelFacCorrect .row .row_span{line-height:35px;font-size:13px
}
.hotelFacCorrect .row .openTime{height:35px;border-radius:3px;border:1px solid #d1d1d5;margin-right:20px;padding-left:10px
}
.hotelFacCorrect .save{width:50%;display:block;height:50px;margin:40px auto 0 auto
}


