
#firstset .tabHeader{height:60px;margin-top:70px
}
#firstset .tabHeader .headerList{height:100%;background-color:#fff;line-height:60px
}
#firstset .messagebox{width:100%;margin-top:70px;height:40px;border-bottom:2px solid #f5f5f5;background-color:#fff
}
#firstset .messagebox span{width:40px;height:40px;display:block;text-align:center;cursor:pointer
}
#firstset .messagebox span img{margin-top:13px
}
#firstset .form{padding-left:140px;width:100%;background-color:#fff;padding-top:30px
}
#firstset .form .avatar-uploader .el-upload{border:1px dashed #8c939d;border-radius:6px;cursor:pointer;position:relative;width:490px;height:305px;overflow:hidden
}
#firstset .form .avatar-uploader .el-upload:hover{border-color:#20a0ff
}
#firstset .form .avatar-uploader-icon{font-size:28px;color:#8c939d;width:490px;height:305px;line-height:305px;text-align:center
}
#firstset .form .avatar{width:490px;height:305px;display:block
}
#firstset .form p{font-size:14px;color:#9fa9b9;margin-top:18px
}
#firstset .form .el-textarea{width:490px;height:136px;margin-top:40px
}
#firstset .form .el-textarea textarea{height:136px;resize:none;font-family:'';font-size:16px
}
#firstset .form button{width:110px;height:40px;background-color:#2b9ef7;color:#fff;font-size:16px;cursor:pointer;outline:none;border:none;border-radius:2px;margin-top:60px;margin-bottom:60px
}


