.allRoute .el-dialog__wrapper .el-dialog .el-dialog__body .dia_content .hotel_item .hotel_r{float:left;height:100%;width:370px;padding:20px 20px;position:relative;border-bottom:1px solid #f1f4f8;box-sizing:border-box
