
.tabHeader{height:60px;margin-top:70px
}
.tabHeader .titlebox{width:100%;height:40px;color:#a1a5ae;background-color:#eef1f6
}
.tabHeader .titlebox .subitem{height:40px;width:calc(14.28571429%);display:inline-block;font-size:15px;line-height:40px;text-align:center
}
.tabHeader .supplierbox{width:100%
}
.tabHeader .supplierbox li{height:80px;font-size:16px;width:100%;background-color:#fff;border-bottom:1px solid #f5f5f5;cursor:pointer;overflow:hidden;white-space:nowrap;text-overflow:ellipsis
}
.tabHeader .supplierbox li span{width:calc(14.28571429%);display:inline-block;line-height:80px;text-align:center;overflow:hidden;white-space:nowrap;text-overflow:ellipsis
}
.tabHeader .supplierbox li span:nth-child(4),.tabHeader .supplierbox li span:nth-child(5),.tabHeader .supplierbox li span:nth-child(6),.tabHeader .supplierbox li span:nth-child(7){text-indent:-34px
}
.tabHeader .supplierbox li .bluetext{color:#20a0ff
}
.tabHeader .supplierbox li .greentext{color:green
}
.tabHeader .el-dialog__body{padding-bottom:80px
}
.tabHeader .item{font-size:0
}
.tabHeader .item .countryList{display:inline-block;width:50%
}
.tabHeader .item .el-autocomplete{float:left;width:70%
}
.tabHeader .item .hotelList{display:inline-block;width:50%
}
.tabHeader .item .islandList{display:inline-block
}
.tabHeader .item div{font-size:14px
}
.tabHeader .item h3{font-size:20px;color:#fff;font-weight:normal
}
.tabHeader .item .hotelInput{width:100%
}
.tabHeader .item .hotelInput .el-input{width:121%
}
.tabHeader .item p{font-size:14px;color:#fff;margin-bottom:5px;text-align:left
}
.tabHeader .item .el-row{margin-top:5px
}
.tabHeader .item .orderType,.tabHeader .item .orderSource{display:inline-block;width:50%;margin-top:5px
}
.tabHeader .item .itemFooter{width:100%;height:60px;position:absolute;left:0;bottom:0;background-color:#273142;border-radius:4px;display:-ms-flexbox;display:flex;-ms-flex-pack:end;justify-content:flex-end;-ms-flex-align:center;align-items:center
}
.tabHeader .item .itemFooter button{height:40px;margin-right:30px
}
.tabHeader .item .el-input input{background-color:#273142;border:1px solid #313d4f;border-radius:3px;color:#fff
}
.tabHeader .el-dialog__footer{background-color:#273142
}
.tabHeader .el-dialog{background-color:#222c3c
}
.tabHeader .headerList{height:100%;background-color:#fff;line-height:60px
}
.tabHeader .headerList li{float:left;padding:0 30px;text-align:center;font-size:16px;color:#c8d4dd;cursor:pointer
}
.tabHeader .headerList li .addOrder{border-radius:15px;cursor:pointer
}
.tabHeader .headerList .rightLi{float:right
}
.tabHeader .headerList .active{color:#354052;position:relative
}
.tabHeader .headerList .active:after{position:absolute;content:'';width:100%;height:3px;background-color:#279df4;bottom:0;left:0
}


