#shopfirstset .right_index ul li{width:60px;height:60px;display:block;color:#9fa9b9;border:1px solid #9fa9b9;box-sizing:border-box;line-height:60px;margin-bottom:12px;text-align:center;cursor:pointer
}
#shopfirstset .right_index ul li.liactive{border:1px solid #6e9bf1;color:#6e9bf1;background:#b6caf0
}


