
#module .tabHeader{height:60px;margin-top:70px
}
#module .tabHeader .headerList{height:100%;background-color:#fff;line-height:60px
}
#module .messagebox{width:100%;margin-top:70px;background-color:#fff
}
#module .messagebox ul li{width:100%;height:150px;font-size:16px;padding-left:45px;position:relative;border-bottom:1px solid #ebebeb;overflow:hidden
}
#module .messagebox ul li p{font-size:22px;font-weight:bold;margin-top:46px
}
#module .messagebox ul li span{margin-top:17px;font-size:16px;display:inline-block;color:#9fa9b9
}
#module .messagebox ul li button{width:110px;height:40px;cursor:pointer;outline:none;border:none;font-size:16px;color:#fff;background-color:#2b9ef7;position:absolute;right:38px;top:50px;border-radius:4px
}


