
#setting{padding-left:40px;color:#333333;font-size:14px
}
#setting .el-row{margin-top:25px
}
#setting .time p{line-height:35px
}
#setting .time input{-webkit-appearance:none;-moz-appearance:none;appearance:none;background-color:#fff;background-image:none;border-radius:4px;border:1px solid #bfcbd9;box-sizing:border-box;color:#1f2d3d;display:inline-block;font-size:inherit;height:36px;line-height:1;outline:0;padding:3px 10px;transition:border-color .2s cubic-bezier(.645, .045, .355, 1);border-radius:0
}
#setting .place p{line-height:35px
}
#setting .place .el-input__inner{border-radius:0;width:50%
}
#setting .el-button{width:45%;padding-bottom:20px
}
#setting .save{margin-top:40px;margin-bottom:20px
}


