
#login{min-width:1280px;display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center;-ms-flex-align:center;align-items:center;background-color:#212531;width:100%;height:100%
}
#login .loginBox{position:relative;width:30%;background-color:#ffffff;border-radius:6px;height:370px;padding:45px
}
#login .loginBox .loginImg{position:absolute;text-align:center;top:-35px;left:50%;margin-left:-35px
}
#login .loginBox .el-form-item__label{font-size:14px;color:#7f8fa4;font-weight:bold
}
#login .loginBox .el-input__inner{border:1px #dfe3e9 solid;background-color:#f8fafc
}
#login .loginBox .el-button--success{margin:0 auto;display:block;margin-top:116px;width:86%;border-radius:4px;font-size:14px;padding:10px 0
}
#login .loginBox .passport_box .el-form-item__content .el-input{width:60%
}
#login .loginBox .passport_box .el-form-item__content .el-input .el-input__inner{width:100%
}
#login .loginBox .getyzm{width:102px;float:right;height:36px;cursor:pointer;border:none;outline:none;background:#13ce66;border-radius:2px;color:#fff;margin-left:4%
}
#login .loginBox .getyzm.getyzm2{cursor:default;background:#fff;color:#ccc
}


