
.fade-enter-active{transition:all .5s ease
}
.fade-leave-active{transition:all 0s ease
}
.fade-enter,.fade-leave-active{opacity:0;transform:translateY(-50px)
}
#shopanalysis{padding-top:70px;min-width:1300px
}
#shopanalysis .tabbox{width:100%;height:75px;background:#fff;border-bottom:1px solid #ebebeb
}
