}
#shopanalysis .tabbox .tabul li.active{border-bottom:2px solid #3889da
}
#shopanalysis .tabbox .clear{clear:both
}
#shopanalysis .selectgroup{height:60px;width:100%;margin-top:20px;padding-left:120px
}
#shopanalysis .selectgroup .el-select{float:left;height:60px
}
