}
#shopanalysis .selectgroup .rightpart .block .el-date-editor{height:60px
}
#shopanalysis .selectgroup .rightpart .block .el-date-editor .el-input__inner{height:60px;font-size:16px
}
#shopanalysis .selectgroup .rightpart span{float:left;margin-top:20px;margin-left:10px;margin-right:10px
}
#shopanalysis .selectgroup .rightpart .checkbtn{float:right;width:90px;height:60px;color:#fff;background:#3889da;font-size:16px;outline:none;border:none;border-radius:4px;margin-left:20px;cursor:pointer
}
#shopanalysis .selectgroup .clear{clear:both
}
#shopanalysis #tubiao{width:100%;height:600px;margin-top:60px
}
#shopanalysis .tipsbox{width:100%;height:600px;margin-top:60px
}
#shopanalysis .checkresult{width:100%;margin-top:30px;padding-left:120px;margin-bottom:60px;padding-right:120px
}
#shopanalysis .checkresult .checktab{width:200px;height:40px;margin-top:20px;margin-bottom:20px
}
#shopanalysis .checkresult .checktab ul .checkItem{width:100px;height:40px;font-size:16px;border-bottom:1px solid #ccc;text-align:center;line-height:40px;cursor:pointer;float:left
}
#shopanalysis .checkresult .checktab ul .checkItem.active{border-bottom:2px solid #3889da
}
