}
#shopanalysis .checkresult .checkinset{width:100%;height:600px;border:1px solid #ebebeb
}
#shopanalysis .checkresult .checkinset .checkhead{height:60px;width:100%;background:#484e54;font-size:16px;display:-ms-flexbox;display:flex
}
#shopanalysis .checkresult .checkinset .checkhead .head_item{height:60px;line-height:60px;text-align:left;padding-left:20px;-ms-flex:1;flex:1;color:#fff
}
