}
#shopanalysis .checkresult .checkinset .checkcontent{width:100%
}
#shopanalysis .checkresult .checkinset .checkcontent ul{width:100%
}
#shopanalysis .checkresult .checkinset .checkcontent ul li{min-height:120px;display:-ms-flexbox;display:flex;width:100%;border-bottom:1px solid #ebebeb;box-sizing:border-box;background:#fff
}
#shopanalysis .checkresult .checkinset .checkcontent ul li .li_item{-ms-flex:1;flex:1;height:120px;line-height:120px;text-align:left;padding-left:20px
}
#shopanalysis .checkresult .checkinset .checkcontent ul li .li_item.li_three{-ms-flex:3;flex:3
}
#shopanalysis .checkresult .checkinset .checkcontent ul li .li_item.li_three .details{font-size:15px;color:#3889da;cursor:pointer;margin-left:15px
}
#shopanalysis .checkresult .checkinset .checkcontent ul .detail_box{width:100%
