}
#shopanalysis .checkresult .checkinset .checkcontent ul .detail_box ul .detail_item{min-height:120px;width:100%;background:#ddd;display:-ms-flexbox;display:flex
}
#shopanalysis .checkresult .checkinset .checkcontent ul .detail_box ul .detail_item .detail_one{-ms-flex:1;flex:1;min-height:120px;line-height:120px;text-align:left;padding-left:20px
}
#shopanalysis .checkresult .checkinset .checkcontent ul .detail_box ul .detail_item .detail_one.detail_three{-ms-flex:3;flex:3;line-height:25px
}
#shopanalysis .checkresult .checkinset .checkcontent ul .detail_box ul .detail_item .detail_one.detail_three p{width:80%;text-align:justify;margin-top:20px;overflow:hidden;margin-bottom:10px
}


