
.SelectMain{width:200px;height:38px;position:relative;border:1px solid #313d4f;background:#273142;border-radius:5px;z-index:1000
}
.SelectMain .select_input{width:100%;height:100%;line-height:38px;color:#fff;padding-left:10px
}
.SelectMain .select_btn{position:absolute;right:10px;top:12px;color:#fff;font-size:12px;cursor:pointer
}
.SelectMain .SelectList{width:98%;background:#273142;position:absolute;top:40px;border:1px solid #313d4f;border-radius:5px
}
.SelectMain .selectItem{cursor:pointer;width:100%;height:38px;line-height:38px;color:#fff
}
.SelectMain .selectItem:hover{background:#275c89
}
.SelectMain .selectcolor{width:15px;height:15px;border-radius:3px;display:inline-block;float:left;margin:12px 15px 0 20px
}
.SelectMain .selectText{border:none;background:#273142;height:30px;padding-left:7px;font-size:14px;color:#fff;width:130px
}
.SelectMain .new_button{color:#556276;font-size:14px;padding-left:20px
}
.selectfade-enter-active,.selectfade-leave-active{transition:all .5s
}
.selectfade-enter,.selectfade-leave-active{opacity:0;transform:translateY(20px)
}


