
#home{height:100%;min-width:1200px
}
#home .wrapper{height:100%
}
#home .wrapper .homeIndex{height:100%;z-index:1000;background-color:#202631;position:relative
}
#home .wrapper .homeIndex .homeIndex-list{margin-top:105px
}
#home .wrapper .homeIndex .homeIndex-list li{display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center;margin-bottom:30px;cursor:pointer
}
#home .wrapper .homeIndex .homeIndex-list li .icon-color_light{color:#fff
}
#home .wrapper .homeIndex .homeIndex-list li .icon-color_dark{color:#4d515a
}
#home .wrapper .homeIndex .homeIndex-list li i{font-size:24px
}
#home .wrapper .homeLeft{position:relative;height:100%;background-color:#222c3c;z-index:1000
}
#home .wrapper .homeLeft .el-menu--dark{background-color:#222c3c
}
#home .wrapper .homeLeft .el-menu-item .el-menu-item-group__title,#home .wrapper .homeLeft .el-submenu .el-menu-item-group__title{display:none
}
#home .wrapper .homeLeft .el-menu-item .iconfont,#home .wrapper .homeLeft .el-submenu .iconfont{margin-right:15px;font-size:18px
}
#home .wrapper .homeLeft .el-menu-item .el-menu .el-menu-item,#home .wrapper .homeLeft .el-submenu .el-menu .el-menu-item{padding-left:60px !important
}
#home .wrapper .homeLeft .el-menu-item .point,#home .wrapper .homeLeft .el-submenu .point{float:right;display:inline-block;line-height:20px;margin-top:18px;padding:3px 13px 3px 13px;background-color:red;border-radius:30%;color:#fff
}
#home .wrapper .homeLeft .header{position:relative;height:100px;display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center;-ms-flex-align:center;align-items:center
}
#home .wrapper .homeLeft .header:after{position:absolute;content:'';height:2px;width:90%;left:17px;bottom:0;text-align:center;background-color:#2a3547
}
#home .wrapper .homeLeft .listItem{position:relative;padding:25px
}
#home .wrapper .homeLeft .listItem h3{font-size:14px;margin-bottom:25px;color:#5b6779;font-weight:normal
}
#home .wrapper .homeLeft .listItem h3 span{float:right
}
#home .wrapper .homeLeft .listItem a{text-decoration:none
}
#home .wrapper .homeLeft .listItem .router-link-active{color:#fff !important
}
#home .wrapper .homeLeft .listItem .router-link-active:before{position:absolute;content:'';width:0;height:0;top:50%;left:-23.5px;margin-top:-4.5px;border:5px solid transparent;border-left:5px solid #29a4f3
}
#home .wrapper .homeLeft .listItem .listContent{display:inline-block;position:relative;color:#858f9b;cursor:pointer;font-size:14px
}
#home .wrapper .homeLeft .triangle:before{position:absolute;content:'';width:0;height:0;top:50%;left:-23.5px;margin-top:-4.5px;border:5px solid transparent;border-left:5px solid #29a4f3
}
#home .wrapper .homeLeft .listItem:after{position:absolute;content:"";bottom:0;left:50%;margin-left:-45%;width:90%;height:2px;background-color:#2a3547;text-align:center
}
#home .wrapper .homeRight{height:100%;display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column;overflow-y:scroll
}
#home .wrapper .homeRight .header{height:70px;display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;-ms-flex-pack:end;justify-content:flex-end;border-bottom:2px solid #f5f5f5;position:fixed;left:0;top:0;width:100%;z-index:999;background-color:#fff
}
#home .wrapper .homeRight .header .messagebtn{width:30px;height:30px;position:relative;top:6px;left:16px;cursor:pointer
}
#home .wrapper .homeRight .header .messagebtn img{margin-left:5px;margin-top:5px
}
#home .wrapper .homeRight .header .header_left{margin-right:10px;margin-top:9px
}
#home .wrapper .homeRight .header .header_left .header_Search_input{border:none;padding-left:10px;width:130px;height:30px;text-align:center;outline:none;border-bottom:1px solid #dbe1ed
}
#home .wrapper .homeRight .header .header_left .header_Search_btn{cursor:pointer;vertical-align:middle;color:#dbe1ed
}
#home .wrapper .homeRight .header .header_right .userImg{display:inline-block;width:40px;height:40px;overflow:hidden;border-radius:50%;margin-right:20px
}
#home .wrapper .homeRight .header .header_right .userImg img{width:100%;vertical-align:middle
}
#home .wrapper .homeRight .header .header_right .userDetails{display:inline-block;vertical-align:middle;margin-right:15px
}
#home .wrapper .homeRight .header .header_right .userDetails p:nth-child(1){font-size:14px;color:#6cbbf6
}
#home .wrapper .homeRight .header .header_right .userDetails p:nth-child(2){font-size:14px;color:#c0c0c0
}
#home .wrapper .homeRight .header .header_right .el-dropdown{display:inline-block;vertical-align:middle;margin-right:20px
}


