
#processsec{min-height:100%;background-color:#eff3f6;min-width:1200px;padding-bottom:45px
}
#processsec .content{margin-left:20px;margin-right:20px;display:-ms-flexbox;display:flex
}
#processsec .content .finish{margin:100px auto
}
#processsec .content .finish .finishlogo{height:120px;width:120px;border-radius:50%;background-color:#21ed51;margin:0 auto
}
#processsec .content .finish .finishlogo div{margin:60px auto;display:block
}
