}
#processsec .content .finish .finishtips{color:#414f64;font-size:20px;text-align:center;margin:0 auto;width:100%;display:block;margin-top:40px
}
#processsec .content .finish .nextstep{margin:0 auto;display:block;width:300px;margin-top:40px;height:50px;background-color:#fff;border:1px solid #21ed51;box-sizing:border-box;border-radius:2px;color:#21ed51;font-size:14px
}


