
#shopsort .shopcontent{padding-top:70px;background:#fff
}
#shopsort .shopcontent .clear{clear:both
}
#shopsort .shopcontent .shop_l{width:70%;float:left;border-right:1px solid #d6d6d6;box-sizing:border-box
}
#shopsort .shopcontent .shop_l .top_input{width:100%;height:170px;border-bottom:1px solid #d6d6d6
}
#shopsort .shopcontent .shop_l .top_input .number_input{width:300px;height:70px;margin-top:50px;margin-left:40px;font-size:18px;border:1px solid #d6d6d6;text-indent:10px
}
#shopsort .shopcontent .shop_l .sort_content{width:100%;min-height:460px;border-bottom:1px solid #d6d6d6;padding-left:40px
}
#shopsort .shopcontent .shop_l .sort_content ul{padding-bottom:100px
}
#shopsort .shopcontent .shop_l .sort_content ul .sort_item{width:50%;height:40px;float:left;margin-top:50px;font-size:18px;line-height:40px
}
#shopsort .shopcontent .shop_l .sort_content ul .sort_item .sort_input{width:80px;height:40px;margin-left:20px;border:none;outline:none;border-bottom:1px solid #d6d6d6;font-size:20px;color:#4a8bec;font-weight:bold;text-align:center
}
#shopsort .shopcontent .shop_l .sort_content ul .clear{clear:both
}
#shopsort .shopcontent .shop_l .btn_part{height:100px;width:100%
}
#shopsort .shopcontent .shop_l .btn_part button{width:130px;height:50px;background:#2b9ef7;color:#fff;border:none;outline:none;margin-top:25px;margin-left:40px;cursor:pointer;border-radius:2px;font-size:18px
}
#shopsort .shopcontent .shop_r{width:30%;float:right;padding:20px 24px 0 24px;overflow:auto
}
#shopsort .shopcontent .shop_r ul .country_item{width:100%;height:220px;cursor:pointer;margin-bottom:50px;position:relative
}
#shopsort .shopcontent .shop_r ul .country_item .chooseit{height:18px;width:18px;position:absolute;left:-33px;transform:rotate(45deg);top:101px;background:#000
}
#shopsort .shopcontent .shop_r ul .country_item .mask_item{position:absolute;top:0;left:0;bottom:0;height:190px;right:0;background:#efefef;opacity:.9
}
#shopsort .shopcontent .shop_r ul .country_item img{width:100%;height:190px;display:block;border:1px solid #ccc;box-sizing:border-box
}
#shopsort .shopcontent .shop_r ul .country_item p{text-align:center;line-height:20px;font-size:18px;margin-top:10px
}
#shopsort .shopcontent .shop_r ul .country_item p.nochoose{color:#d6d6d6
}


