
.contractDetail{min-height:100%;background-color:#eff3f6;overflow:auto
}
.contractDetail .header{display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;height:60px;position:relative;width:100%;background:#fff
}
.contractDetail .header p{position:absolute;left:20px
}
.contractDetail .header .header_name{margin:0 auto
}
.contractDetail .header .header_name span{color:#848c98
}
.contractDetail .body{width:1145px;margin:30px auto;min-height:100%;border-radius:5px;background:#fff
}
.contractDetail .body .body_nav{display:table;margin:0 auto
}
.contractDetail .body .body_nav>li{position:relative;display:inline-block;margin:0 10px;padding:0 40px;line-height:91px;height:90px;color:#848c98;cursor:pointer
}
.contractDetail .body .body_nav>li.active{color:#1875f0;border-bottom:2px solid #1875f0
}
.contractDetail .body .body_nav .combo{position:absolute;left:-30px;width:200px;text-align:left;top:90px;line-height:50px;padding-left:10px;border:1px solid #dee9f2;border-bottom:0;background:#fff;z-index:999
}
.contractDetail .body .body_nav .combo>li{position:relative;border-bottom:1px solid #dee9f2;color:#848c98
}
.contractDetail .body .body_nav .combo>li i{font-size:13px;position:absolute;top:18px;right:8px
}
.contractDetail .body .body_nav .combo .fixa_combo{padding-left:20px
}
.contractDetail .body .body_nav .combo .fixa_combo>li{position:relative
}
.contractDetail .body .body_nav .combo .fixa_combo>li i{font-size:13px;position:absolute;top:18px;right:8px
}
.contractDetail .body .body_nav .combo .fixa_combo .add_input input{border:none;border-bottom:1px solid #0081ff;height:30px;width:100%
}
.contractDetail .body .body_nav .combo .affirm{margin-left:40px
}
.contractDetail .body .body_nav .fade-enter-active,.contractDetail .body .body_nav .fade-leave-active{transition:opacity .5s
}
.contractDetail .body .body_nav .fade-enter,.contractDetail .body .body_nav .fade-leave-active{opacity:0
}


