
.newActivity{height:100%;display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column
}
.newActivity .wrap{width:100%
}
.newActivity .header{height:66px;width:100%;border-bottom:1px solid #e6eaee;line-height:66px;padding-left:20px
}
.newActivity .header p{display:inline-block
}
.newActivity .header p .el-date-editor.el-input{width:175px
}
.newActivity .header ul{float:right
}
.newActivity .header ul li{float:left;padding:0 10px;margin:0 10px;height:65px;cursor:pointer
}
.newActivity .header .active{border-bottom:3px solid #1a91eb
}
.newActivity .body{-ms-flex:1;flex:1
}
.newActivity .body .destAct{display:-ms-flexbox;display:flex;-ms-flex-pack:justify;justify-content:space-between;-ms-flex-align:center;align-items:center;border-bottom:2px solid #e6eaee;padding:20px
}
.newActivity .body .actType{font-size:16px;color:#504f4f;margin-bottom:5px
}
.newActivity .body .actPersons{font-size:16px;color:#bcbdc1
}
.newActivity .body .actPersons .info{color:#3c3a3a;margin-left:5px;letter-spacing:5px
}
.newActivity .body .actPersons .method{display:inline-block;width:200px
}
.newActivity .body .noData{width:100%;height:100%;display:-ms-flexbox;display:flex;-ms-flex-line-pack:center;align-content:center;-ms-flex-pack:center;justify-content:center;font-size:20px;color:#adb6c4
}
.newActivity .footer{padding:0 50px;height:70px;border-top:none;display:-ms-flexbox;display:flex;-ms-flex-pack:end;justify-content:flex-end;padding-top:20px
}
.newActivity .footer .el-button--success{height:38px
}
.newActivity .footer button{float:right
}


