
[v-bloak]{display:none
}
.countryList{width:100%;overflow:hidden
}
.countryList .noInfo{font-size:20px;color:#adb6c4;margin-top:20px;text-align:center
}
.countryList .main{min-width:1280px;font-size:0;padding-left:70px;padding-right:70px;margin-right:-45px;text-align:left;margin-bottom:100px
}
.countryList .main .countryItem{height:230px;width:245px;margin-top:45px;margin-right:46px;display:inline-block;margin-bottom:20px
}
.countryList .main .countryItem .pic{cursor:pointer;width:100%;height:155px
}
.countryList .main .countryItem .pic .cPic{width:100%;height:100%
}
.countryList .main .countryItem .pic .noPic{width:100%;height:100%;background-color:#c6cbcf;display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center;-ms-flex-align:center;align-items:center
}
.countryList .main .countryItem .content{-ms-flex:1;flex:1;font-size:14px;padding:15px 10px;background-color:#fff;position:relative
}
.countryList .main .countryItem .content p{text-align:left;width:100%
}
.countryList .main .countryItem .content .text1{color:#2a2323;margin-bottom:3px
}
.countryList .main .countryItem .content .text2{color:#848c98
}
.countryList .main .countryItem .content .more{cursor:pointer;color:#848c98;position:absolute;top:50%;margin-top:-7px;right:17px
}
.countryList .main .countryItem .content .moreList{position:absolute;right:0;bottom:-83px;background-color:#fff;font-size:0;border-radius:5px
}
.countryList .main .countryItem .content .moreList li{color:#acacac;display:inline-block;font-size:14px;padding:15px
}
.countryList .main .countryItem .content .moreList .edit,.countryList .main .countryItem .content .moreList .check,.countryList .main .countryItem .content .moreList .del{cursor:pointer;text-align:center;width:50px;height:50px
}
.countryList .main .countryItem .content .moreList .edit p,.countryList .main .countryItem .content .moreList .check p,.countryList .main .countryItem .content .moreList .del p{margin-top:5px;text-align:center
}
.countryList .main .countryItem .content .moreList .edit:hover,.countryList .main .countryItem .content .moreList .check:hover,.countryList .main .countryItem .content .moreList .del:hover{background-color:#299ef5;color:#fff;border-radius:50%
}
.countryList .main .countryItem .content .listfade-enter-active,.countryList .main .countryItem .content .listfade-leave-active{transition:all .5s
}
.countryList .main .countryItem .content .listfade-enter,.countryList .main .countryItem .content .listfade-leave-active{opacity:0
}


