
.app .div1{width:60px;height:60px;background:red
}
.app .btn{position:fixed;bottom:50px;right:10px;z-index:10;width:50px;height:50px;line-height:50px;border-radius:50%;border:none;outline:none;color:#fff;font-size:18px;background:blue
}
.app .menu{position:fixed;bottom:50px;right:10px;width:50px;height:50px;border-radius:50%;transition:all .7s ease-in
}
.app .menu.move-enter-active .inner{transform:translate3d(0, 0, 0);transition-timing-function:cubic-bezier(0, .57, .44, 1.97)
}
.app .menu.move-enter-active .inner-1{transition-delay:.1s
}
.app .menu.move-enter-active .inner-2{transition-delay:.2s
}
.app .menu.move-enter-active .inner-3{transition-delay:.3s
}
.app .menu.move-enter .inner,.app .menu.move-leave-active .inner{transition-timing-function:ease-in-out
}
.app .menu.move-enter .inner-1,.app .menu.move-leave-active .inner-1{transform:translate3d(0, 60px, 0);transition-delay:.3s
}
.app .menu.move-enter .inner-2,.app .menu.move-leave-active .inner-2{transform:translate3d(40px, 40px, 0);transition-delay:.2s
}
.app .menu.move-enter .inner-3,.app .menu.move-leave-active .inner-3{transform:translate3d(60px, 0, 0);transition-delay:.1s
}
.app .menu .inner{display:inline-block;position:absolute;width:30px;height:30px;line-height:30px;border-radius:50%;background:red;text-align:center;color:#fff;transition:all .4s
}
.app .menu .inner-1{top:-50px;left:10px
}
.app .menu .inner-2{left:-30px;top:-30px
}
.app .menu .inner-3{left:-50px;top:10px
}


