
.contractCostDetail{border-top:1px solid #efecec
}
.contractCostDetail .main{width:1000px;margin:0 auto
}
.contractCostDetail .main .left{width:250px;display:inline-block;vertical-align:top;padding-top:70px
}
.contractCostDetail .main .left .unit{border-bottom:1px solid #efecec;padding-bottom:30px
}
.contractCostDetail .main .left ul{width:250px
}
.contractCostDetail .main .left ul li.room_li{height:30px;margin:20px 0;color:#e2e2e2;cursor:pointer;position:relative
}
.contractCostDetail .main .left ul li.room_li i{position:absolute;right:30px;top:5px
}
.contractCostDetail .main .left ul li.btn{margin-top:20px
}
.contractCostDetail .main .left ul li.active{color:#000;border-right:2px solid #0081ff
}
.contractCostDetail .main .left .row{margin-bottom:50px
}
.contractCostDetail .main .content{border-left:1px solid #efecec;padding:60px 0 0 0;display:inline-block;width:700px;vertical-align:top
}
.contractCostDetail .main .content .name{color:#666666;margin-bottom:20px
}
.contractCostDetail .main .content .btn button{width:90%;height:40px
}
.contractCostDetail .main .content .row{margin-bottom:20px
}


