
.hotelRoomCorrect{width:100%;position:relative;padding-left:20px
}
.hotelRoomCorrect .name{margin:10px 0
}
.hotelRoomCorrect .correct{position:absolute;right:0;top:0
}
.hotelRoomCorrect .en_title{color:#adafc0;margin:10px 0 20px 0
}
.hotelRoomCorrect .row{margin-bottom:30px
