.hotelRoomCorrect .pic_list .uploadbox .el-upload-list li .el-upload-list__item-status-label{-webkit-transform:rotate(45deg)
}
.hotelRoomCorrect .pic_list .uploadbox .el-upload-list li .el-upload-list__item-status-label .el-icon-check{-webkit-transform:rotate(-45deg) scale(.8)
