}
.hotelRoomCorrect .pic_list .uploadbox div .el-upload .el-icon-plus{color:#fff;position:relative;top:-24px
}
.hotelRoomCorrect .pic_list .uploadbox .el-dialog__wrapper .el-dialog .el-dialog__header{display:inherit !important
}
.hotelRoomCorrect .pic_list .uploadbox .el-dialog__body{display:block !important
