
.contractCombo{border-top:1px solid #efecec
}
.contractCombo .main{width:1000px;margin:0 auto
}
.contractCombo .main .left{display:inline-block;padding:70px 0 0 0;vertical-align:top
}
.contractCombo .main .left ul{width:160px
}
.contractCombo .main .left ul li{height:20px;margin:20px 0;color:#e2e2e2;cursor:pointer
}
.contractCombo .main .left ul li.active{color:#000;border-right:2px solid #0081ff
}
.contractCombo .main .content{border-left:1px solid #efecec;display:inline-block;width:700px;padding:60px 0 0 40px;vertical-align:top
}
.contractCombo .main .content .name{color:#666666;margin-bottom:20px
}
.contractCombo .main .content .btn button{width:90%;height:40px
}
.contractCombo .main .content .row{margin-bottom:20px
}


