
@import 'assets/fonts/iconfont.css';
@import 'assets/fonts/iconfont2.css';
@import 'assets/fonts/iconfont3.css';
#app{width:100%;height:100%
}
.login-enter-active{animation:login-in .5s
}
.login-leave-active{animation:login-out .2s
}
@keyframes login-in{
0%{transform:scale(0)
}
50%{transform:scale(1.05)
}
100%{transform:scale(1)
}
}
@keyframes login-out{
0%{transform:scale(1)
}
50%{transform:scale(.95)
}
100%{transform:scale(0)
}
}
.fade-enter-active,.fade-leave-active{transition:opacity .5s
}
.fade-enter,.fade-leave-active{opacity:0
}
body,div,dl,dt,dd,ul,ol,li,h1,h2,h3,h4,h5,h6,pre,code,form,fieldset,legend,input,textarea,p,blockquote,th,td{margin:0;padding:0
}
html{height:100%
}
body{width:100%;height:100%;font-family:Helvetica Neue,Helvetica,PingFang SC,Hiragino Sans GB,Microsoft YaHei,Noto Sans CJK SC,WenQuanYi Micro Hei,Arial,sans-serif;background:#eff3f6
}
ul{list-style-type:none
}
input::-webkit-outer-spin-button,input::-webkit-inner-spin-button{-webkit-appearance:none !important;margin:0
}
*{box-sizing:border-box
}
.peoInfo{width:210px;min-height:230px;padding:15px
}
.peoInfo h3{margin:10px 0
}
.peoInfo div{font-size:14px;color:#7f8fa4;margin-bottom:10px
}
.peoInfo .peoInfo_top{height:50px;line-height:50px;width:100%;color:#fff;margin:0
}


