
.fade-enter-active{transition:all .5s ease
}
.fade-leave-active{transition:all 0s ease
}
.fade-enter,.fade-leave-active{transform:scale(0);opacity:0
}
#material .mask{position:absolute;left:0;right:0;bottom:0;top:0;background-color:#000;opacity:.3;z-index:100
}
#material .distribution{width:800px;height:400px;position:absolute;z-index:101;background-color:#fff;left:55%;top:50%;margin-left:-400px;margin-top:-200px;padding-top:80px;padding-left:100px;padding-right:100px
}
#material .distribution .row{margin-bottom:30px
}
#material .distribution .row .distributionit{width:190px;height:50px;margin:0 auto;display:block;margin-top:30px;background-color:#4a8bec;color:#fff;font-size:16px
}
#material .distribution .row .el-input__inner{width:420px;height:60px;font-size:16px
}
#material .distribution .el-icon-close{position:absolute;cursor:pointer;right:20px;top:20px
}
#material .tabHeader{height:60px;margin-top:70px
}
#material .subtitle{width:100%;height:62px;color:#888;background-color:#eef1f6;display:-ms-flexbox;display:flex
}
#material .subtitle .subitem{height:62px;-ms-flex:1;flex:1;font-size:16px;line-height:62px;text-align:left;text-indent:30px
}
#material .subtitle .subitem button{width:122px;height:40px;color:#fff;background-color:#1d95ee;border-radius:4px;border:none;outline:none;cursor:pointer;font-size:16px;line-height:40px
}
#material .supplierbox{width:100%
}
#material .supplierbox .nulltips{width:100%;height:200px;font-size:20px;line-height:200px;text-align:center;background-color:#fff;padding-bottom:600px
}
#material .supplierbox li{height:80px;font-size:16px;width:100%;background-color:#fff;border-bottom:1px solid #f5f5f5;display:-ms-flexbox;display:flex
}
#material .supplierbox li span{-ms-flex:1;flex:1;display:inline-block;line-height:80px;text-indent:30px;overflow:hidden;text-overflow:ellipsis;white-space:nowrap
}
#material .supplierbox li span button{width:60px;background-color:#fff;cursor:pointer;outline:none;border-radius:2px;font-size:14px;letter-spacing:1px;height:40px
}
#material .supplierbox li span button.cheek_btn{color:#1d95ee;border:1px solid #1d95ee;margin-right:6px;box-sizing:border-box
}
#material .supplierbox li span button.del_btn{color:#df275b;border:1px solid #df275b;box-sizing:border-box
}


