
#caseset .tabHeader{height:60px;margin-top:70px
}
#caseset .tabHeader .headerList{height:100%;background-color:#fff;line-height:60px
}
#caseset .messagebox{width:100%;margin-top:70px;height:40px;border-bottom:2px solid #f5f5f5;background-color:#fff
}
#caseset .messagebox span{width:40px;height:40px;display:block;text-align:center;cursor:pointer
}
#caseset .messagebox span img{margin-top:13px
}
#caseset .form{width:100%;padding-left:50px;padding-right:50px;padding-top:65px;background-color:#fff
}
#caseset .form ul{position:relative;padding-bottom:50px
}
#caseset .form ul li{width:280px;height:230px;display:inline-block;margin-right:60px;margin-bottom:80px
}
#caseset .form ul li .li_top{height:167px;position:relative;width:100%
}
#caseset .form ul li .li_top img{width:100%;height:167px
}
#caseset .form ul li .li_top span{position:absolute;left:15px;color:#fff;bottom:15px;max-width:258px;overflow:hidden;text-overflow:ellipsis;white-space:nowrap
}
#caseset .form ul li .li_bottom{width:100%;height:63px
}
#caseset .form ul li .li_bottom p{display:inline-block;line-height:70px;color:#9fa9b9
}
#caseset .form ul li .li_bottom input{width:106px;height:53px;border:none;border-bottom:1px solid #d6d6d6;outline:none;line-height:53px;text-align:center;font-size:16px;padding-top:10px
}
#caseset .form ul button{position:absolute;left:0;bottom:0;width:110px;height:40px;background-color:#2b9ef7;color:#fff;font-size:16px;border:none;outline:none;border-radius:4px;cursor:pointer;bottom:30px
}


