
.allRoute{margin-top:70px
}
.allRoute .headerList{background:#fff;height:60px;line-height:60px
}
.allRoute .headerList li{float:left;padding:0 30px;text-align:center;font-size:16px;color:#c8d4dd;cursor:pointer
}
.allRoute .headerList li .addOrder{border-radius:15px;cursor:pointer
}
.allRoute .headerList .rightLi{float:right;margin-right:60px
}
.allRoute .headerList .active{color:#354052;position:relative
}
.allRoute .headerList .active:after{position:absolute;content:'';width:100%;height:3px;background-color:#279df4;bottom:0;left:0
}
.allRoute .table .state_two{color:#3599f8
}
.allRoute .table .tabel_btn{background:#fff
}
.allRoute .table .tabel_btn_info{color:#20A0FF
}
.allRoute .table .tabel_btn_success{color:#13CE66
}
.allRoute .table .tabel_btn_warning{color:#F7BA2A
}
.allRoute .table .tabel_btn_danger{color:#FF4949
}


