
.activityInfoCorrect{width:100%;position:relative;padding-left:20px
}
.activityInfoCorrect .name{margin:10px 0
}
.activityInfoCorrect .row{margin-bottom:30px
}
.activityInfoCorrect .row .row_name{color:#adafc0
}
.activityInfoCorrect .row .add{float:right
}
.activityInfoCorrect .row .close_wrap{float:right
}
.activityInfoCorrect .row .close_wrap .close{color:#000
}
.activityInfoCorrect .row .row_com{padding-left:10px;padding-right:10px
}
.activityInfoCorrect .row .row_com1{padding-left:10px;padding-right:10px;margin-left:10px
}
.activityInfoCorrect .row .row_span{line-height:34px
}
.activityInfoCorrect .row .openTime{height:35px;border-radius:3px;border:1px solid #d1d1d5;margin-right:20px;padding-left:10px
}
.activityInfoCorrect .row .temslot{margin-right:20px
}
.activityInfoCorrect .pic_list{width:100%;padding-bottom:20px
}
.activityInfoCorrect .pic_list .uploadbox{margin-top:20px;margin-bottom:20px
}
.activityInfoCorrect .pic_list .uploadbox .el-upload-list li{width:240px;margin-right:15px;height:160px
}
.activityInfoCorrect .pic_list .uploadbox .el-upload-list li .el-upload-list__item-status-label{-webkit-transform:rotate(45deg)
}
.activityInfoCorrect .pic_list .uploadbox .el-upload-list li .el-upload-list__item-status-label .el-icon-check{-webkit-transform:rotate(-45deg) scale(.8)
}
.activityInfoCorrect .pic_list .uploadbox div{display:inline
}
.activityInfoCorrect .pic_list .uploadbox div .el-upload{display:inline-block
}
.activityInfoCorrect .pic_list .uploadbox div .el-upload-list .is-success{width:240px;height:160px
}
.activityInfoCorrect .pic_list .uploadbox div .is-uploading{width:240px;height:160px
}
.activityInfoCorrect .pic_list .uploadbox div .el-upload{width:240px;height:160px;background-color:#b3c5e1;border:none
}
.activityInfoCorrect .pic_list .uploadbox div .el-upload .el-icon-plus{color:#fff
}
.activityInfoCorrect .pic_list .uploadbox .el-dialog__wrapper .el-dialog .el-dialog__header{display:inherit !important
}
.activityInfoCorrect .pic_list .uploadbox .el-dialog__body{display:block !important
}
.activityInfoCorrect .pic_list .pic_add{position:relative;margin:25px 15px 25px 0;vertical-align:bottom;cursor:pointer;color:#fff;width:153px;height:100px;border-radius:5px;background:#c5ddf0;text-align:center;line-height:100px;display:inline-block
}
.activityInfoCorrect .save{width:50%;display:block;height:50px;margin:10px auto 0 auto
}


