
.Carousel .el-dialog__headerbtn{display:none
}
.Carousel .el-dialog{background:transparent;box-shadow:none
}
.Carousel .el-dialog--tiny{width:70%
}
.Carousel .slide-btn{position:relative;display:inline-block;width:9%;height:453px;cursor:pointer
}
.Carousel .slide-btn>div{position:absolute;left:0;right:0;top:0;bottom:0;margin:auto;display:block;width:50px;height:50px;border-radius:25px;display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center;-ms-flex-align:center;align-items:center;font-size:20px
}
.Carousel .slide-btn .ban{background:#b5b5b5;color:#8d8d91
}
.Carousel .slide-btn .active{background:#fff;color:#37394c
}
.Carousel .carousel-wrap{position:relative;display:inline-block;height:453px;width:80%;overflow:hidden;background-color:#fff
}
.Carousel .carousel-wrap .slide-ul{width:100%;height:100%
}
.Carousel .carousel-wrap .slide-ul li{position:absolute;width:100%;height:100%
}
.Carousel .carousel-wrap .slide-ul li img{width:100%;height:100%
}
.Carousel .Carousel_bottom{text-align:center;width:200px;margin:35px auto
}
.Carousel .Carousel_bottom .Carousel_btn{display:inline-block;text-align:center;font-size:15px;color:#fff;cursor:pointer
}
.Carousel .Carousel_bottom .Carousel_btn i{display:block;margin:0 auto;font-size:20px;padding-bottom:10px
}
.Carousel .listright-enter-active{transition:all 1s ease;transform:translateX(0)
}
.Carousel .listright-leave-active{transition:all 1s ease;transform:translateX(100%)
}
.Carousel .listright-enter{transform:translateX(-100%)
}
.Carousel .listright-leave{transform:translateX(0)
}
.Carousel .listleft-enter-active{transition:all 1s ease;transform:translateX(0)
}
.Carousel .listleft-leave-active{transition:all 1s ease;transform:translateX(-100%)
}
.Carousel .listleft-enter{transform:translateX(100%)
}
.Carousel .listleft-leave{transform:translateX(0)
}


