
#allorder{min-width:1400px
}
#allorder .tabHeader{height:60px;margin-top:70px
}
#allorder .tabHeader .el-dialog__body{padding-bottom:80px
}
#allorder .tabHeader .item{font-size:0
}
#allorder .tabHeader .item .countryList{display:inline-block;width:50%
}
#allorder .tabHeader .item .el-autocomplete{float:left;width:70%
}
#allorder .tabHeader .item .hotelList{display:inline-block;width:50%
}
#allorder .tabHeader .item .islandList{display:inline-block
}
#allorder .tabHeader .item div{font-size:14px
}
#allorder .tabHeader .item h3{font-size:20px;color:#fff;font-weight:normal
}
#allorder .tabHeader .item .hotelInput{width:100%
}
#allorder .tabHeader .item .hotelInput .el-input{width:121%
}
#allorder .tabHeader .item p{font-size:14px;color:#fff;margin-bottom:5px;text-align:left
}
#allorder .tabHeader .item .el-row{margin-top:5px
}
#allorder .tabHeader .item .orderType,#allorder .tabHeader .item .orderSource{display:inline-block;width:50%;margin-top:5px
}
#allorder .tabHeader .item .itemFooter{width:100%;height:60px;position:absolute;left:0;bottom:0;background-color:#273142;border-radius:4px;display:-ms-flexbox;display:flex;-ms-flex-pack:end;justify-content:flex-end;-ms-flex-align:center;align-items:center
}
#allorder .tabHeader .item .itemFooter button{height:40px;margin-right:30px
}
#allorder .tabHeader .item .el-input input{background-color:#273142;border:1px solid #313d4f;border-radius:3px;color:#fff
}
#allorder .tabHeader .headerList{height:100%;background-color:#fff;line-height:60px
}
#allorder .tabHeader .headerList li{float:left;padding:0 30px;text-align:center;font-size:16px;color:#c8d4dd;cursor:pointer
}
#allorder .tabHeader .headerList li .addOrder{border-radius:15px;cursor:pointer
}
#allorder .tabHeader .headerList .active{color:#354052;position:relative
}
#allorder .tabHeader .headerList .active:after{position:absolute;content:'';width:100%;height:3px;background-color:#279df4;bottom:0;left:0
}
#allorder .subtitle{width:100%;height:40px;color:#c8d6ef;background-color:#eef1f6;display:-ms-flexbox;display:flex
}
#allorder .subtitle .subitem{height:40px;-ms-flex:1;flex:1;text-indent:10px;font-size:15px;line-height:40px;margin-right:40px;text-align:left
}
#allorder .allorderbox{width:100%
}
#allorder .allorderbox .nulltips{width:100%;height:200px;font-size:20px;line-height:200px;text-align:center;background-color:#fff;padding-bottom:600px
}
#allorder .allorderbox .orderItem{min-height:110px;font-size:16px;width:100%;background-color:#fff;border-bottom:1px solid #f5f5f5;display:-ms-flexbox;display:flex;cursor:pointer
}
#allorder .allorderbox .orderItem span{-ms-flex:1;flex:1;margin-right:10px;margin-right:30px;display:inline-block;padding-top:20px;padding-bottom:20px;text-align:justify;line-height:24px;padding-left:10px
}
#allorder .allorderbox .orderItem button{width:60px;height:40px;color:#279df4;border:1px solid #279df4;box-sizing:border-box;background:#fff;cursor:pointer;outline:none;border-radius:2px
}


