
#commonstep .step{height:72px;margin:0 auto;overflow:hidden;text-align:center;border-top:1px solid #e8e9ed;border-bottom:1px solid #e8e9ed
}
#commonstep .step .step_inset{height:28px;margin-top:22px
}
#commonstep .step .step_inset .stepitem{margin-right:30px;display:inline-block
}
#commonstep .step .step_inset .stepitem .radius{cursor:pointer;width:28px;height:28px;border-radius:50%;border:1px solid #e6eaee;box-sizing:border-box;color:#7f8fa4;text-align:center;line-height:28px;background-color:#fff;display:inline-block
}
#commonstep .step .step_inset .stepitem .emphasize{color:#fff !important;background-color:#2399f2 !important;border:1px solid #1f8ad9 !important
}
#commonstep .step .step_inset .stepitem .emphasize1{cursor:pointer;color:#fff !important;background-color:#2399f2 !important;border:1px solid #1f8ad9 !important
}
#commonstep .step .step_inset .stepitem .dg{cursor:pointer;color:#7f8fa4 !important;background-color:#fff !important;border:1px solid #dfe2e5 !important
}
#commonstep .step .step_inset .stepitem .stepinfo{color:#7f8fa4;font-size:14px;margin-left:8px
}


