
.contractInfo{width:100%;position:relative;padding:0 0 30px 15px
}
.contractInfo h3{margin-bottom:5px
}
.contractInfo .en_title{font-size:14px;color:#adafc0;margin:0 0 40px 0
}
.contractInfo .correct{position:absolute;right:0;top:0
}
.contractInfo .row{margin-bottom:30px
}
.contractInfo .row .row_name{color:#adafc0
}
.contractInfo .row .row_row{margin-bottom:15px
}
.contractInfo .row .font_gray{color:#adafc0
}
.contractInfo .row .season_box{border:1px solid #e1ebf3;height:40px;position:relative;line-height:40px;font-size:13px;margin:0 20px 20px 0;padding-left:5px;cursor:pointer
}
.contractInfo .row .season_box div{width:100%;height:100%
}
.contractInfo .row .season_box i{position:absolute;top:12px;right:4px;color:#aeaeb4
}


